*,
*:before,
*:after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.submit {
  display: flex;
  align-items: center;
  justify-content: center;
}

.App {
  padding: 20px;
}

.navbar {
  display: flex;
  background: #4682b4;
  justify-content: space-around;
  flex-direction: row;
  align-items: center;
}

.logo-navbar {
  width: 20%;
  height: 60%;
  margin: 9px;
}

.navbar-logged {
  display: flex;
  margin: 10px;
}

.navbar-button {
  margin: 10px;
  padding: 10px;
  height: 35px;
  border-radius: 5%;
}

/* -------------------PAGINA HOME -------------------- */

.home {
  background-image: url(https://wallpaperset.com/w/full/7/c/c/392452.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 92.3vh;
  overflow: hidden;
}

.home-titles {
  display: flex;
  justify-content: center;
  text-align: center;
  flex-direction: column;
}

.home h1 {
  font-style: italic;
  margin: 25px 0;
  font-size: 35px;
}
.home h2 {
  font-style: italic;
  font-size: 20px;
  color: dimgray;
}
.cards-list {
  z-index: 0;
  width: 100%;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
}

.card {
  margin: 30px auto;
  width: 150px;
  height: 150px;
  border-radius: 40px;
  box-shadow: 5px 5px 30px 7px rgba(0, 0, 0, 0.25),
    -5px -5px 30px 7px rgba(0, 0, 0, 0.22);
}

.card .card_image {
  width: inherit;
  height: inherit;
  border-radius: 40px;
}

.card .card_image img {
  width: 150px;
  height: 150px;
  border-radius: 40px;
  object-fit: cover;
  border: 3px solid white;
}

.card .card_title {
  text-align: center;
  border-radius: 0px 0px 40px 40px;
  font-family: sans-serif;
  font-weight: bold;
  font-size: 30px;
  margin-top: -80px;
  height: 40px;
}

.card:hover {
  transform: scale(0.9, 0.9);
  box-shadow: 5px 5px 30px 15px rgba(0, 0, 0, 0.25),
    -5px -5px 30px 15px rgba(0, 0, 0, 0.22);
}

.carddetail-image {
  display: block;
  width: 50%;
  height: 50%;
  border-radius: 5%;
}

.title-white {
  color: white;
}

/* --------------------------LOGIN PAGE-------------------------------- */

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

.login {
  margin: 0 auto;
  padding: 10px 0;
  background-image: url(https://wallpaperset.com/w/full/7/c/c/392452.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 92vh;
  overflow: hidden;
}

.login-header {
  color: black;
  text-align: center;
  font-size: 150%;
  margin: 30px;
}

.login-button {
  margin: 10px;
  padding: 5px;
  border: 10px;
  width: 100px;
  border-radius: 3rem;
  border: 1px solid black;
  background-color: rgb(233, 233, 233);
}
.login-form h3 {
  text-align: center;
  color: black;
}
.login-form {
  box-sizing: border-box;
  padding-top: 15px;
  padding-bottom: 10%;
  margin: 5% auto;
  text-align: center;
}
.login input[type="text"],
.login input[type="password"] {
  max-width: 400px;
  width: 80%;
  line-height: 3em;
  font-family: "Ubuntu", sans-serif;
  margin: 1em 2em;
  border-radius: 3rem;
  border: 2px solid #f2f2f2;
  outline: none;
  padding-left: 10px;
}
.login-form input[type="button"] {
  height: 30px;
  width: 100px;
  background: #fff;
  border: 1px solid #f2f2f2;
  border-radius: 20px;
  color: slategrey;
  text-transform: uppercase;
  font-family: "Ubuntu", sans-serif;
  cursor: pointer;
}

.sign-up {
  color: #f2f2f2;
  margin: 10px 0;
  cursor: pointer;
  text-decoration: underline;
}
.no-access {
  color: #e86850;
  margin: 10px 0px;
  text-decoration: underline;
  cursor: pointer;
}
.try-again {
  color: #f2f2f2;
  text-decoration: underline;
  cursor: pointer;
}

/*--------------------------------offers------------------------*/

ol,
ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

.title-offer {
  display: flex;
  justify-content: center;
  font-size: 30px;
  margin-top: 15px;
}

.band {
  height: auto;
  max-width: 1500px;
  margin: 0 auto;
  padding: 10px 50px;
  border: 1px solid rgba(0, 0, 0, 0);
  border-top: none;
  border-bottom: none;
}

.band h1 {
  margin-top: 10%;
  font-size: 3rem;
  color: #000000;
  font-weight: 100;
  letter-spacing: 0.25px;
}

.band p {
  letter-spacing: 0.5px;
  color: #484848;
  font-size: 1.1rem;
  padding-top: 0.75em;
}

.band101 {
  color: purple;
  text-decoration: none;
  letter-spacing: 0.5px;
  font-size: 1.1rem;
}

.band101:hover {
  text-decoration: underline;
  color: purple;
}

/*Cards*/

.cardsoffers {
  display: flex;
  flex-wrap: wrap;
  margin-top: 5%;
  margin-bottom: 1%;
  justify-content: center;
}

.cardsoffers a:hover {
  text-decoration: none;
}

.cardsoffers-item {
  display: flex;
  padding: 5px;
}

.cardContainer {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 30px;
  justify-content: center;
}

.cardoffer {
  display: flex;
  flex-direction: column;
  border-radius: 5%;
  box-shadow: 0 20px 40px -14px rgba(0, 0, 0, 0.25);
  width: 250px;
  height: 350px;
}

.offer-image {
  width: 100%;
  height: 50%;
  margin-bottom: 40px;
  border-radius: 5%;
}

.cardoffer-content {
  display: flex;
  flex-direction: column;
  padding: 2px;
  margin-top: 15px;
}

.cardoffer-title {
  color: #000000;
  font-size: 20px;
  font-weight: 500;
  padding-bottom: 1rem;
}

.cardoffer-text {
  font-size: 15px;
  line-height: 1.5;
  margin: 5px 0px;
}

.cardsoffer-button {
  background-color: white;
  border: 2px solid #4682b4;
  color: #4682b4;
  padding: 10px;
  text-align: center;
  margin-top: 10px;
  border-radius: 10px;
}

.cardoffer-image {
  display: block;
  width: 100%;
  height: 50%;
  border-radius: 5%;
}

/*Cards*/

/* ------------------------OFFERCREWDETAILS------------------- */

.offerDetail {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 10px;
  line-height: 45px;
  flex-wrap: wrap;
  align-items: center;
}

.details-buttons{
  display: flex;
  flex-direction: row;
  margin-top: 15px;
}

.offerCrewPhoto{
  display: flex;
  justify-content: center;
}

.offerDetail-info {
  font-size: 20px;
  margin-top: 15px;
  width: 75%;
}

/*-------- PROFILE PAGE -------- */
.profile-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 10px;
}

.title-profile {
  font-size: 25px;
  margin: 20px 5px;
}

.profile-links {
  display: flex;
  justify-content: space-evenly;
  margin: 25px;
}
.profile-button {
  margin: 5px;
  padding: 5px;
  border: 5px;
  width: 110px;
  border-radius: 3rem;
  border: 1px solid black;
  background-color: rgb(233, 233, 233);
}
.profile-list {
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 20px;
  line-height: 30px;
  text-align: center;
}
.profile-list-title {
  font-size: 20px;
}
.profile-list-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 15px;
  border: 2px solid #4682b4;
  margin: 15px;
  border-radius: 5%;
  box-shadow: 5px 8px 8px #888888;
  width: 30vh;
  height: 32vh;
}
.image-offer {
  width: 80%;
  height: 60%;
  border-radius: 5%;
  margin-top: 7px;
}
/* ------------------------ EDIT INFO PAGES------------------- */

.edit {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 10px;
  line-height: 40px;
  flex-wrap: nowrap;
}

.edit-info {
  font-size: 20px;
  margin-top: 30px;
  line-height: 50px;
}

.profile-image {
  width: 40%;
  height: 40%;
  margin: 10px;
  border-radius: 5%;
}

