  }
  .profile-list-info {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 35px;
    border: 2px solid #4682b4;
    margin: 30px;
    border-radius: 5%;
    box-shadow: 5px 8px 8px #888888;
    padding: 20px;
    width: 60vh;
    height: 60vh;
    flex-wrap: nowrap;
  }
  .image-offer {
    width: 40vh;
    height: 40vh;
    border-radius: 5%;
    margin-bottom: 10px;
  }

  .cards-profileLaptop {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin-top: 75px;
    flex-wrap: wrap;
  }

  .profile-image {
    width: 20%;
    height: 20%;
    margin: 10px;
    border-radius: 5%;
  }

  /* Login */

  .login {
    background-image: url(https://wallpaperset.com/w/full/7/c/c/392452.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    height: 88.7vh;
    overflow: hidden;
  }

  .login-header {
    color: black;
    text-align: center;
    font-size: 150%;
    margin-top: 40px;
  }

  .login-button {
    margin: 10px;
    padding: 5px;
    border: 10px;
    width: 100px;
    border-radius: 3rem;
    border: 1px solid black;
    background-color: rgb(233, 233, 233);
  }
  .login-form h3 {
    text-align: center;
    color: black;
  }
  .login-form {
    box-sizing: border-box;
    padding-top: 15px;
    padding-bottom: 10%;
    margin: 5% auto;
    text-align: center;
    line-height: 40px;
  }
  .login input[type="text"],
  .login input[type="password"] {
    max-width: 400px;
    width: 80%;
    line-height: 3em;
    font-family: "Ubuntu", sans-serif;
    margin: 1em 2em;
    border-radius: 3rem;
    border: 2px solid #f2f2f2;
    outline: none;
    padding-left: 10px;
  }
  .login-form input[type="button"] {
    height: 30px;
    width: 100px;
    background: #fff;
    border: 1px solid #f2f2f2;
    border-radius: 20px;
    color: slategrey;
    text-transform: uppercase;
    font-family: "Ubuntu", sans-serif;
    cursor: pointer;
  }
}

